import Vue from 'vue'
import App from './App'
import router from './router'
import Axios from 'axios'
import './assets/markdown-github.css'

Vue.prototype.$http = Axios

Vue.config.productionTip = false

/* eslint-disable no-new */

Vue.filter('formatDate',function(value){
  if(!value) return ;
  var lastTime = new Date(value)
  var time = (new Date().getTime() - lastTime.getTime())/1000 // Áé∞Âú®ÁöÑÊó∂Èó¥-ÊúÄÂêé‰∏ÄÊ¨°ÂõûÂ§çÊó∂Èó¥=Êó∂Èó¥Â∑Æ„ÄÇÂçï‰ΩçÊòØÊØ´Áßí
  if(time<0){
    return ;
  }else if(time < 60){
    return Math.round(time) + ' ÁßíÂâç'
  }else if(time < 3600 ){
    return Math.round(time/60) + ' ÂàÜÈíüÂâç'
  }else if(time < 86400){
    return Math.round(time/3600) + ' Â∞èÊó∂Ââç'
  }else if(time < 2592000){
    return Math.round(time/86400) + ' Â§©Ââç'
  }else if(time < 31104000){
    return Math.round(time/2592000) + ' ‰∏™ÊúàÂâç'
  }else{
    return Math.round(time/31104000) + ' Âπ¥Ââç'
  }
})
Vue.filter('tabFormatter',function(item){
  if(item.top){
    return 'ÁΩÆÈ°∂'
  }else if(item.good){
    return 'Á≤æÂçé'
  }else if(item.tab === 'share'){
    return 'ÂàÜ‰∫´'
  }else if(item.tab === 'ask'){
    return 'ÈóÆÁ≠î'
  }else if(item.tab === 'job'){
    return 'ÊãõËÅò'
  }else{
    return 'Â∏ñÂ≠ê'
  }
})

new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})





